@import './../../../styles/variables.scss';

.home__section__1 {
  background-color: $bg;
  height: 100vh;
  padding-top: $headerHeight;
  // background-repeat: no-repeat;
  // background-size: cover;
  // background-position: center;
  // background-attachment: fixed;

  h1 {
    font-family: $Poppins;
    font-size: 10vh;
  }

  img {
    width: 18vh;
    margin-top: 5vh;
  }
}

.home__section__2 {
  background-color: $dark;
  height: 100vh;
  width: 100%;
  padding-top: $headerHeight;

  h3 {
    text-align: center;
    color: $light;
  }

  img {
    margin: 30px auto;
    display: block;
    width: 80%;
    object-fit: cover;
    transition: transform 0.2s;
    opacity: 1;

    &:hover {
      transform: scale(1.1);
    }
  }

  h6 {
    color: $light;
    text-align: center;
    font-style: italic;
    font-weight: 700;
  }

  p {
    color: $light;
    text-align: center;
  }
}
